const add_salt_form_string = function(e, salt = '8') {

  let is = (e % 2 == 0) ? 1 : 0
  return e + is + salt + ''
}

const remove_salt_form_string = function(e, salt = '8') {
  return e.substring(0, e.length - 2)
}

const full_number = function(rec, len = 4) {

  const n = rec.length
  rec = rec.toString()

  if (len > 2) {
    if (rec.length == 3) {
      return '0' + rec
    }
    else if (rec.length == 2) {
      return '00' + rec
    } 
    else if (rec.length == 1) {
      return '000' + rec
    }
    
  } else {
    if (rec.length == 1) {
      return '0' + rec
    }
  }
  
  return rec
}

export default {
  full_number,
  add_salt_form_string,
  remove_salt_form_string
}


