import Vue from 'vue'
import Vuex from 'vuex'

import salt from '../es/scret/salt'
import db from '../es/tools/storage'
import config from '../config'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    named: '',
    pass: '',
    is_num_order: true,
    isLogin: false
  },
  mutations: {
    changeName(state, v) {
      state.named = v
    },
    changePass(state, v) {
      state.pass = v
    },
    changeLogin(state, v) {

    },

    // 注销登录
    loginOut(state, v) {
      state.named = ''
      state.pass = ''
      state.isLogin = false
      db._set('invoice_user', '')
    },

    // 保存登录
    async saveLogin(state, data) {
      state.named = data.named
      state.isLogin = true

      data.alive = Date.now()
      await db._setAsync('invoice_user', data)
    },
    // 检查登录
    async checkLoginAlive(state) {
      const user = await db._getAsync('invoice_user', true)
      
      if (user) {
        const t = user.alive
        if (Date.now() - t > config.activeTime) {
          state.isLogin = false
          alert('您的【' + user.named + '】賬戶的登錄已過期！！！')
          await db._setAsync('invoice_user', '')
        }
      }
    }
  },
  actions: {

  },
  // 过滤获取东西
  getters: {

    getUserName(state) {
      let is_login = true
      let user = db._get('invoice_user')

      if ((user == undefined) || (user == '') || (user == null)) {
        is_login = false
      } else {
        user = JSON.parse(user)
        state.isLogin = true
        state.named = user.named
      }

      return v => {
        
        if (!is_login) {
          if (state.named == '') { return false }
        }
        return state.named
      }
    }
  },
  modules: {

  }
})

export default store


