
const Dashboard = () => import('@/screen/Dashboard/Dashboard')
const DashboardNew = () => import('@/screen/Dashboard/DashboardNew')


import ListingRouter from './dashboard/listing'
import MemberRouter from './dashboard/member'
import FreightRouter from './dashboard/freight'
import PricesRouter from './dashboard/prices'
import TagRouter from './dashboard/tag'
import RecordRouter from './other/record'

export default [
    {
      path: '',
      redirect: 'dashboard'
    },
    {
      path: 'dashboard/',
      component: Dashboard,
      meta: {
        title: '主面板'
      }
    },
    {
      path: 'dashboard_new/',
      component: DashboardNew,
      meta: {
        title: '仪表盤'
      }
    },
    ...ListingRouter,
    ...MemberRouter,
    ...FreightRouter,
    ...PricesRouter,
    ...TagRouter,

    ...RecordRouter
  ]


